@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300i|Martel+Sans|Montserrat:900i);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  position: relative;
  font-family: "Martel Sans";
  color: #1D142E;
  margin: 0;
  background-color: #ffffff;
}

p {
  font-family: "Martel Sans";
  color: #1D142E;
}

section{
  height: 100vh;
}

.logo{
  font-size: 3.3em;
  height: 124px;
  font-family: Montserrat;
  font-weight: 900;
  font-style: oblique;
}

h1, h2, h3, h4, h5 {
  font-family: "Josefin Sans";
  font-weight: 300;
  font-style: oblique;
  margin: 0;

}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  width: 100%;
}

.app > * {
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
}

div.img-card{
  position: relative;
  display: grid;

}

.img-look{
  width: 30vw;
  margin: 0;
  padding:0;
}

.img-icon{
  width: 35px;
  height: 35px;
  position: absolute;
top: 50%;
  left: 50%;
  opacity: 0;
}

.img-card:hover  > .img-icon { opacity: 1 ; } 

/* fallback for earlier versions of Firefox */

@supports not (flex-wrap: wrap) {
  div.masonry { display: block; }
  div.masonry img {  
  display: inline-block;
  vertical-align: top;
  }
}

input {
  width: 100%;
  box-sizing: border-box;
  border-radius: 0;
}

*:focus {
    outline: none;
}

select {
  width: 100%;
  padding: 16px 20px;
  border: none;
  border-radius: 4px;
  background-color: #f1f1f1;
}

.clear {
  clear: both;
}

/*nav-bar*/
.navBar{
  background-color: #EFFAEE;
  width: 100vw;
  height: 120px;
  display: block;
}

.navBar h1 {
  margin: 0;
  display: inline-block;
  text-align: center;
}

.navBar ul {
  height: 100px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.navLinks {
  height: 100px;
  list-style: none;
  float: left;
  padding: 5px;
  margin: 0;
  display: inline-block;

}

.navLinks a {
  display: block;
  text-align: center;
  text-decoration: none;
  height: 100px;
}

li.navLinks.nav-right {
    float: right;
}

/* Nav transition effects */
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}


.headerImg {
  height: 100px;
/*  display: inline-block;
  text-align: left;*/
}

.center{
  display: block;
  margin: 0 auto;
  text-align: center;
}

.left {
  float: left;
}

.filter-bar{
  display: block;
/*  padding: 10px;*/
}

.looks-page{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.masonryApi { 

  background-color: #EC4068;
  background: linear-gradient(to bottom, #F45F55, #EC4068, #AE2E7A);

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;

  padding: 0px;
  height: 400vh;
  width: 120vw;
  overflow-x: hidden !important;

} 

div.masonryApi:hover .img-look { opacity: 1; }
div.masonryApi:hover .img-look:hover { opacity: 0.5; } 


.look-card{
  padding: 75px 30px;
}

.quoteBar {

  background-image: url(/static/media/bg01.891389e4.jpg);
  background-size: cover;
  -webkit-order: 4;
          order: 4;

}

.quote-screen{
  background-color: #ce93d8;
  height: 100%;
  opacity: .5;
  z-index: 1;
}

.quoteBar blockquote{
  z-index: 10;
}

.component-heading{
  padding: 10px 0 0 0;
  text-align: center;
}

.footerBar {
  background-color: #EFFAEE;
  position: fixed;
  bottom: 0;
  width: 100%;
  -webkit-order: 6;
          order: 6;

  text-align: center;

}

.landing{
  -webkit-order: 1;
          order: 1;
}

.link:hover {
  cursor:pointer;
}

.FilterBar {
    padding: 20px;
}
.content-wrapper section {
    -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -o-filter: blur(10px);
    -ms-filter: blur(10px);
    filter: blur(10px);
}

.content-wrapper {
    margin:auto;
    padding:0 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.content-wrapper>img {
    width:100%;
}

.registration-page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;

  background-color: white;
  padding: 30px;
}

.registration-page form {
  margin: 0;
}

input.register-form {
	padding: 5px;
	margin: 5px;
}

input[type=submit].register-form {
	padding: 10px;
	color: #EFFAEE;
	text-transform: uppercase;
	background-color: #F45F55;
	border: 1px solid #dfdfdf;
}
.landingImg {
  width: 25vw;
  -webkit-flex: none;
          flex: none;
  -webkit-align-self: center;
          align-self: center;

}

.landing {
	height: 100vh;
	width: 100vw;
	background-color: #EC4068;
	background: linear-gradient(to bottom right, #F45F55, #EC4068, #AE2E7A);

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

h1, h2, h3, h4 {

  -webkit-flex: none;

          flex: none;
  -webkit-align-self: center;
          align-self: center;
}

form {
  margin-top: 20px;
  margin-bottom: 20px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;

}

select, input, button {
  border-width: 0px;
  background-color: #FFFFFF;
	box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  
  width: 30vw;
  margin: 2px;
  padding: 3px;

}

input[type=submit] {
    text-transform: uppercase;
    color: #1D142E;
}

